@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background-color: #f0f7ff;
  color: #1a202c;
}

a {
  text-decoration: none;
  color: inherit;
}

a:hover {
  color: #3b82f6;
  text-decoration: underline;
}
